.wp-express.admin.wrap h2.page-title {
  display: flex;
  align-items: center; }
  .wp-express.admin.wrap h2.page-title span {
    margin-right: .1em; }

.wp-express.field.attachment {
  padding: 0;
  width: 150px;
  float: none;
  text-align: initial; }
  .wp-express.field.attachment .img-container {
    width: 150px;
    height: 150px;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover; }
  .wp-express.field.attachment .btn-remove {
    cursor: pointer;
    position: absolute;
    top: -10px;
    right: -10px;
    width: 30px;
    height: 30px;
    border: 0;
    border-radius: 30px;
    padding: 0;
    background: red;
    color: white; }

.wp-express.option-wrap.checkbox label {
  display: flex;
  align-items: center; }

.wp-express.metabox .wp-express.post-meta-wrap {
  margin-bottom: 1em; }
  .wp-express.metabox .wp-express.post-meta-wrap:last-of-type {
    margin-bottom: 0; }
  .wp-express.metabox .wp-express.post-meta-wrap label {
    font-weight: bold;
    margin-bottom: .5em;
    display: block; }
  .wp-express.metabox .wp-express.post-meta-wrap .field.input input {
    width: 99%; }
  .wp-express.metabox .wp-express.post-meta-wrap.attachment {
    position: static;
    float: none;
    padding: 0;
    text-align: initial;
    width: auto; }
  .wp-express.metabox .wp-express.post-meta-wrap.checkbox label {
    display: flex;
    align-items: center; }
